.App {
  font-family: Montserrat;
}
html, body {
  height: 100%;
  width: 100%;
}
img {
  max-width:100px;
}
pre {
  background-color: gainsboro;
  margin: 20px;
  overflow: hidden;
  border: 1px solid black;
}
code {
  background-color: gainsboro;
}
#editor {
  width:100%;
  height:100%;
  background-color: black;
  color:white;
  overflow-y: auto;
  overflow-x: hidden;
  resize: none;
  margin-left:-1px;
}
blockquote {
  font-style: italic;
}
#preview {
  background-color: yellow;
  width:100%;
  height:100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding:10px;
  margin-top:-15px;
}
#wrap-edit {
  width:100%;
  height:100%;
  margin:0;
  padding:0;
}
.split {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}
.gutter {
  background-color: black;
  background-repeat: no-repeat;
  background-position: 50%;
}
.gutter.gutter-horizontal {
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');
  cursor: col-resize;
}